@charset "utf-8";
@import './default.css';
@import './colorset.css';
@font-face {
   font-family: 'Pretendard-Regular';
   src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
   font-weight: 400;
   font-style: normal;
}
html,body{width:100%;min-width:320px;height:100vh;font-size:16px;}
body{
   font-family:Helvetica,'Pretendard-Regular','NanumSquareRound',sans-serif,Arial;
   background:var(--bgColor);color:var(--fontColor);
}


button{display:flex;justify-content:center;align-items:center;padding:10px;border-radius:10px;cursor:pointer;}

.wrap,
.wrap.off{
   height:100vh;
   display: flex; align-items: center;justify-content: space-around;
   position:relative;overflow-x:hidden;
}
.wrap.on{justify-content:unset;}

.wrap>.inputBox,
.wrap>.inputBox.off{width:100%;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;transition:width .15s;}
.wrap>.inputBox.on{width:calc(100% - 120px);}
.wrap>.inputBox>.input{display:flex;flex-direction:column;justify-content:space-between;align-items:flex-start;padding:30px 0;box-sizing:border-box;}
.wrap>.inputBox>.input>div{display:flex;font-size:1.3rem;line-height:2rem;}
.wrap>.inputBox>.input>div>input{width:5rem;text-align:center;font-size:2rem;background:transparent;color:var(--highlightColor);}
.wrap>.inputBox>.input>div>input:nth-of-type(2){margin-left:10px;}

.wrap>.inputBox>.result{width:100%;display:flex;flex-direction:column;align-items:center;gap:10px;}
.wrap>.inputBox>.result>p{font-size:2.3rem;color:var(--highlightColor);}
.wrap>.inputBox>.result>.btns{display:flex;justify-content:space-between;gap:20px;}
.wrap>.inputBox>.result>.btns>button{background:var(--BtnColor);color:var(--btnFontColor)}

.wrap>.memories,
.wrap>.memories.off{
   height:100%;padding:15px;box-sizing:border-box;
   position:absolute;right:-100%;top:0;
   background:#ededed;transition:right .15s;
}
.wrap>.memories.on{right:0;}

.wrap>.memories>li{width:90px;cursor:pointer;margin-bottom:20px;overflow:hidden;color:var(--btnFontColor);font-weight:bold;font-style:italic;}

.wrap>.memories>li.memoryView,
.wrap>.memories>li.memoryView.on{height:unset;}
.wrap>.memories>li.memoryView.off{height:19px;}
.wrap>.memories>li>.memoryList{margin:5px 0 0 5px;display:flex;flex-direction:column;gap:5px;position:relative;font-weight:normal;font-style:normal;}
.wrap>.memories>li>.memoryList::before{content:'';display:block;width:1px;height:calc(100% - 9px);background:#000;position:absolute;left:-5px;top:0;}
.wrap>.memories>li>.memoryList>li{position:relative;}
.wrap>.memories>li>.memoryList>li::before{content:'';display:block;width:5px;height:1px;background:#000;position:absolute;left:-5px;top:50%;transform:translateY(-50%)}

@media screen and (min-width:768px){
   html,body{font-size:20px;}
   .wrap>.inputBox,
   .wrap>.inputBox.off{flex-direction:row;}
   .wrap>.inputBox>.result{width:unset;}

   .wrap>.inputBox.on{width:calc(100% - 160px);}
   .wrap>.memories>li{width:130px;}
   .wrap>.memories>li.memoryView.off{height:24px;}
   .wrap>.memories>li>.memoryList::before{height:calc(100% - 12px);}
}

@media screen and (min-width:1024px){
   html,body{font-size:30px;}
   .wrap>.inputBox.on{width:calc(100% - 190px);}
   .wrap>.memories>li{width:160px;}
   .wrap>.memories>li.memoryView.off{height:35px;}
   .wrap>.memories>li>.memoryList::before{height:calc(100% - 17px);}
}
